/**
 * VertexShader  FragmentShader
 *
 * GLSL:
 *   1. attribute:  vertex shader 
 *       WebGL  Buffer  Buffer 
 *       Buffer 
 *   2. uniform:  shader uniform  vertex  fragment 
 *       vertex  fragment 
 *   3. varying: varying  vertex  fragment shader 
 *   4. gl_FragColor    GLSL GLSL
 *       GLSLtexture2D()()
 *   5. sampler1D, sampler2D, sampler3D 
 */

export const vertex = /*  */ `
  uniform float time;
  varying vec2 vUv;
  varying vec3 vPosition;
  varying vec2 pixels;
  float PI = 3.141592653589793238;

  void main() {
    vUv = uv;
    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1);
  }`;

export const fragment = /*  */ `
  uniform float time;
  uniform float progress;
  uniform sampler2D uDataTexture;
  uniform sampler2D uTexture;
  
  uniform vec4 resolution;
  varying vec2 vUv;
  varying vec3 vPosition;
  float PI = 3.141592653589793238;
  
  void main() {
    vec2 newUV = (vUv - vec2(0.5)) * resolution.w + vec2(0.5);
    vec4 color = texture2D(uTexture, newUV);
    vec4 offset = texture2D(uDataTexture, vUv);
    gl_FragColor = vec4(vUv, 0.0, 1.0);
    gl_FragColor = vec4(offset.r, 0.0, 0.0, 1.0);
    gl_FragColor = color;
    gl_FragColor = texture2D(uTexture, newUV - 0.02 * offset.rg);
  }`;

export const particles = /*  */ `
  uniform float time;
  varying vec2 vUv;
  varying vec3 vPosition;
  uniform sampler2D texture1;
  float PI=3.141592653589793238;

  void main() {
    vUv = uv;
    vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
    gl_PointSize = 1000. * (1.0 / - mvPosition.z);
    gl_Position = projectionMatrix * mvPosition;
  }`;

export const vertex1 = /*  */ `
  uniform float scale;
  uniform float factor;
  varying vec2 vUv;
  void main() {
    vec3 pos = position;
    pos.x = pos.x + ((sin(uv.y * 3.1415926535897932384626433832795) * factor * 2.0) * 0.125);
    vUv = uv;
    gl_Position = projectionMatrix * modelViewMatrix * vec4(pos,1.);
  }
`;

export const fragment1 = /*  */ `
  uniform sampler2D tex;
  uniform float factor;
  uniform float scale;
  varying vec2 vUv;
  void main() {
    float angle = 0.0;
    vec2 p = (vUv - vec2(0.5, 0.5)) * (1.0 - scale) + vec2(0.5, 0.5);
    vec2 offset = factor / 50.0 * vec2(cos(angle), sin(angle));
    vec4 cr = texture2D(tex, p + offset);
    vec4 cga = texture2D(tex, p);
    vec4 cb = texture2D(tex, p - offset);
    gl_FragColor = vec4(cr.r, cga.g, cb.b, cga.a);
  }`;
